@tailwind base;
@tailwind components;
@tailwind utilities;

body::-webkit-scrollbar{
    display: none;
}

.dropdownView:hover .dropdownBody{
    display: block !important;
}

.image-input{
    padding: 7px 5px !important;
}

.Pagination{
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    gap: 5px;
}

.Pagination .page-num{
    padding: 8px 15px;
    cursor: pointer;
    border-radius: 3px;
    font-weight: 400;
}

.Pagination .page-num:hover{
    background-color: #5AB1BB;
    color: #fff;
}

.Pagination .active{
    background-color: #5AB1BB;
}

.delete-modal{
    position: absolute;
    left: 50%;
}

.loader{
    margin: auto; 
    display: block; 
}